  src: local("Roboto Regular"), local("Roboto-Regular"), url("../fonts/roboto_400.woff2") format("woff2"), url("../fonts/roboto_400.woff") format("woff"); }

@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: local("Roboto Medium"), local("Roboto-Medium"), url("../fonts/roboto_500.woff2") format("woff2"), url("../fonts/roboto_500.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/roboto_700.woff2") format("woff2"), url("../fonts/roboto_700.woff") format("woff"); }

html,
body {
  max-width: 100%;
  height: 100%;
  overflow-x: hidden; }

html {
  box-sizing: border-box; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 140%;
  color: #1f1e25;
  background-color: #ffffff;
  scroll-behavior: smooth;
  scrollbar-width: thin; }

*,
*::before,
*::after {
  box-sizing: inherit; }

main {
  flex: 1 0 auto; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea {
  border-radius: 0; }

svg {
  fill: inherit; }

.no-scroll {
  height: 100%;
  overflow: hidden; }

.visually-hidden:not(:focus),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%); }

.logo {
  flex-shrink: 0;
  display: flex;
  align-items: flex-end; }

.logo__icon {
  margin-right: 8px;
  fill: #2c36f2;
  stroke: #f6f7ff; }

.logo__text {
  fill: #1f1e25; }

@media (max-width: 1023px) {
  .logo__icon {
    width: 26px;
    height: 24px;
    margin-right: 6px; }
  .logo__text {
    width: 101px;
    height: 14px; } }

@media (max-width: 767px) {
  .logo__icon {
    width: 20px;
    height: 19px;
    margin-right: 5px; }
  .logo__text {
    width: 89px;
    height: 13px; } }

.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  line-height: 0; }

.social__item {
  margin-right: 15px; }
  .social__item:last-of-type {
    margin-right: 0; }

@media (max-width: 1023px) {
  .social__item {
    margin-right: 20px; } }

.page-header {
  font-size: 16px;
