  .page-header__inner {
    max-width: 1023px;
    margin: 0 auto;
    padding: 0 45px;
    padding-top: 22px;
    padding-bottom: 23px; }
  .page-header__logo {
    margin-right: 9.7%; }
  .page-header__nav {
    padding-top: 0; }
  .page-header__nav-item {
    margin-right: 21px; }
  .page-header__user-list {
    padding: 0; }
  .page-header__login svg {
    margin-right: 0; }
  .page-header__login span {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden; } }

@media (max-width: 767px) {
  .page-header__inner {
    max-width: 767px;
    margin: 0 auto;
    padding: 0 15px;
    align-items: flex-end;
    padding-top: 11px;
    padding-bottom: 18px; }
  .page-header__nav {
    order: -1;
    margin-right: 14px; }
  .page-header__nav-toggle {
    position: relative;
    padding: 0;
    font: inherit;
    color: inherit;
    border: none;
    display: block;
    width: 16px;
    height: 12px;
    background-color: transparent; }
    .page-header__nav-toggle::before {
      content: "";
